.Page {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: auto;

    padding: 40px 20px;

    &-Head {
        background-color: aqua;
    }

    &-Menu {
        background-color: aquamarine;
    }

    &-Main {
        background-color: beige;
    }

    &-Foot {
        background-color: cadetblue;
    }
}
